"use client"

import { Canvas, useFrame } from "@react-three/fiber"
import { OrbitControls, Float } from "@react-three/drei"
import { useRef, useMemo } from "react"
import * as THREE from "three"

function NeuralNode({
  position,
  delay = 0,
  size = 0.12,
}: { position: [number, number, number]; delay?: number; size?: number }) {
  const meshRef = useRef<THREE.Mesh>(null)
  const glowRef = useRef<THREE.Mesh>(null)

  useFrame((state) => {
    if (meshRef.current) {
      const pulse = 1 + Math.sin(state.clock.elapsedTime * 2.5 + delay) * 0.15
      meshRef.current.scale.setScalar(pulse)
    }
    if (glowRef.current) {
      const glowPulse = 0.6 + Math.sin(state.clock.elapsedTime * 2 + delay) * 0.2
      ;(glowRef.current.material as THREE.MeshBasicMaterial).opacity = glowPulse
    }
  })

  return (
    <group position={position}>
      {/* Glow sphere */}
      <mesh ref={glowRef} scale={2.5}>
        <sphereGeometry args={[size, 16, 16]} />
        <meshBasicMaterial color="#00ffc8" transparent opacity={0.3} depthWrite={false} />
      </mesh>
      {/* Core node */}
      <mesh ref={meshRef}>
        <icosahedronGeometry args={[size, 2]} />
        <meshStandardMaterial
          color="#00ffc8"
          emissive="#00ffc8"
          emissiveIntensity={1.2}
          metalness={0.9}
          roughness={0.1}
        />
      </mesh>
    </group>
  )
}

function Connections({ nodes }: { nodes: [number, number, number][] }) {
  const lineRef = useRef<THREE.LineSegments>(null)
  const particlesRef = useRef<THREE.Points>(null)

  const { geometry, particlePositions } = useMemo(() => {
    const points: THREE.Vector3[] = []
    const particlePos: number[] = []

    // Create connections between layers with more density
    for (let i = 0; i < nodes.length; i++) {
      for (let j = i + 1; j < nodes.length; j++) {
        const dist = Math.sqrt(
          Math.pow(nodes[i][0] - nodes[j][0], 2) +
            Math.pow(nodes[i][1] - nodes[j][1], 2) +
            Math.pow(nodes[i][2] - nodes[j][2], 2),
        )
        if (dist < 1.8) {
          points.push(new THREE.Vector3(...nodes[i]))
          points.push(new THREE.Vector3(...nodes[j]))

          // Add particles along connections
          for (let t = 0; t < 3; t++) {
            const lerp = (t + 1) / 4
            particlePos.push(
              nodes[i][0] + (nodes[j][0] - nodes[i][0]) * lerp,
              nodes[i][1] + (nodes[j][1] - nodes[i][1]) * lerp,
              nodes[i][2] + (nodes[j][2] - nodes[i][2]) * lerp,
            )
          }
        }
      }
    }

    const geometry = new THREE.BufferGeometry().setFromPoints(points)
    return { geometry, particlePositions: new Float32Array(particlePos) }
  }, [nodes])

  useFrame((state) => {
    if (lineRef.current && lineRef.current.material instanceof THREE.LineBasicMaterial) {
      lineRef.current.material.opacity = 0.25 + Math.sin(state.clock.elapsedTime * 1.5) * 0.1
    }
    if (particlesRef.current) {
      particlesRef.current.rotation.z = state.clock.elapsedTime * 0.05
    }
  })

  return (
    <group>
      <lineSegments ref={lineRef} geometry={geometry}>
        <lineBasicMaterial color="#00ffc8" transparent opacity={0.25} linewidth={1} />
      </lineSegments>
      <points ref={particlesRef}>
        <bufferGeometry>
          <bufferAttribute
            attach="attributes-position"
            count={particlePositions.length / 3}
            array={particlePositions}
            itemSize={3}
          />
        </bufferGeometry>
        <pointsMaterial color="#00ffc8" size={0.04} transparent opacity={0.8} sizeAttenuation />
      </points>
    </group>
  )
}

function NeuralNetworkMesh() {
  const groupRef = useRef<THREE.Group>(null)

  // Define more complex node positions for a deeper neural network
  const nodes: [number, number, number][] = useMemo(() => {
    const positions: [number, number, number][] = []

    // Input layer (5 nodes)
    for (let i = 0; i < 5; i++) {
      positions.push([-2.5, (i - 2) * 0.6, (Math.random() - 0.5) * 0.3])
    }

    // Hidden layer 1 (6 nodes)
    for (let i = 0; i < 6; i++) {
      positions.push([-1.2, (i - 2.5) * 0.55, (Math.random() - 0.5) * 0.4])
    }

    // Hidden layer 2 (7 nodes)
    for (let i = 0; i < 7; i++) {
      positions.push([0, (i - 3) * 0.5, (Math.random() - 0.5) * 0.4])
    }

    // Hidden layer 3 (6 nodes)
    for (let i = 0; i < 6; i++) {
      positions.push([1.2, (i - 2.5) * 0.55, (Math.random() - 0.5) * 0.4])
    }

    // Output layer (4 nodes)
    for (let i = 0; i < 4; i++) {
      positions.push([2.5, (i - 1.5) * 0.65, (Math.random() - 0.5) * 0.3])
    }

    return positions
  }, [])

  useFrame((state) => {
    if (groupRef.current) {
      // Slower, more elegant rotation
      groupRef.current.rotation.y = Math.sin(state.clock.elapsedTime * 0.2) * 0.4
      groupRef.current.rotation.x = Math.sin(state.clock.elapsedTime * 0.15) * 0.15
      groupRef.current.rotation.z = Math.sin(state.clock.elapsedTime * 0.1) * 0.05
    }
  })

  return (
    <Float speed={1.5} rotationIntensity={0.15} floatIntensity={0.4}>
      <group ref={groupRef}>
        <Connections nodes={nodes} />
        {nodes.map((pos, i) => (
          <NeuralNode key={i} position={pos} delay={i * 0.3} size={i < 5 || i >= nodes.length - 4 ? 0.14 : 0.1} />
        ))}
      </group>
    </Float>
  )
}

export function NeuralNetwork3D() {
  return (
    <Canvas camera={{ position: [0, 0, 5.5], fov: 45 }}>
      <color attach="background" args={["transparent"]} />
      <ambientLight intensity={0.3} />
      <pointLight position={[10, 10, 10]} intensity={1} color="#ffffff" />
      <pointLight position={[-10, -10, -10]} intensity={0.8} color="#00ffc8" />
      <pointLight position={[0, 5, 5]} intensity={0.5} color="#00ffc8" />
      <fog attach="fog" args={["#0a0f1a", 4, 12]} />
      <NeuralNetworkMesh />
      <OrbitControls
        enableZoom={false}
        enablePan={false}
        autoRotate
        autoRotateSpeed={0.3}
        maxPolarAngle={Math.PI / 1.8}
        minPolarAngle={Math.PI / 2.2}
      />
    </Canvas>
  )
}
