"use client"

import { motion } from "framer-motion"
import { useInView } from "framer-motion"
import { useRef } from "react"
import { Briefcase, ExternalLink } from "lucide-react"

const experiences = [
  {
    role: "Machine Learning Research Intern",
    company: "DeepMind Research Lab",
    companyUrl: "https://deepmind.com",
    period: "Jun 2024 — Aug 2024",
    description: [
      "Developed novel attention mechanisms for transformer models, achieving 15% improvement in inference speed",
      "Collaborated with senior researchers on a paper submitted to NeurIPS 2024",
      "Built automated data pipelines processing 50TB+ of training data using PyTorch and Ray",
    ],
    tech: ["Python", "PyTorch", "CUDA", "Ray", "Transformers"],
  },
  {
    role: "AI/ML Engineering Intern",
    company: "TechCorp AI",
    companyUrl: "https://example.com",
    period: "Jan 2024 — May 2024",
    description: [
      "Designed and deployed a real-time object detection system serving 100K+ daily requests",
      "Optimized model inference latency by 40% through TensorRT integration and model quantization",
      "Implemented A/B testing framework for ML models, improving deployment confidence by 60%",
    ],
    tech: ["TensorFlow", "TensorRT", "Docker", "Kubernetes", "FastAPI"],
  },
  {
    role: "Undergraduate Research Assistant",
    company: "Stanford AI Lab",
    companyUrl: "https://ai.stanford.edu",
    period: "Sep 2023 — Dec 2023",
    description: [
      "Contributed to open-source NLP toolkit used by 5,000+ researchers worldwide",
      "Implemented state-of-the-art text classification models achieving 94% accuracy on benchmark datasets",
      "Published findings in undergraduate research symposium, winning Best Paper Award",
    ],
    tech: ["Python", "Hugging Face", "scikit-learn", "NLTK", "spaCy"],
  },
]

function ExperienceCard({ experience, index }: { experience: (typeof experiences)[0]; index: number }) {
  const ref = useRef(null)
  const isInView = useInView(ref, { once: true, margin: "-50px", amount: 0.3 })

  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, x: -60, filter: "blur(10px)" }}
      animate={isInView ? { opacity: 1, x: 0, filter: "blur(0px)" } : { opacity: 0, x: -60, filter: "blur(10px)" }}
      transition={{
        duration: 0.6,
        delay: index * 0.15,
        ease: [0.25, 0.46, 0.45, 0.94],
      }}
      className="relative pl-10 pb-12 last:pb-0"
    >
      <div className="absolute left-0 top-0 bottom-0 w-px bg-gradient-to-b from-primary/50 via-border to-border" />

      <motion.div
        initial={{ scale: 0 }}
        animate={isInView ? { scale: 1 } : { scale: 0 }}
        transition={{ duration: 0.4, delay: index * 0.15 + 0.2, type: "spring" }}
        className="absolute left-0 top-2 -translate-x-1/2 w-4 h-4 rounded-full bg-primary border-4 border-background shadow-[0_0_12px_rgba(0,255,200,0.5)]"
      />

      <div className="glass-border">
        <div className="glossy-card group p-6 rounded-xl transition-all duration-300">
          <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-2 mb-3">
            <div>
              <h3 className="text-lg font-semibold text-foreground group-hover:text-primary transition-colors">
                {experience.role}
              </h3>
              <a
                href={experience.companyUrl}
                target="_blank"
                rel="noopener noreferrer"
                className="inline-flex items-center gap-1 text-primary hover:underline"
              >
                {experience.company}
                <ExternalLink className="w-3 h-3" />
              </a>
            </div>
            <span className="text-sm font-mono text-muted-foreground px-3 py-1 rounded-full bg-secondary/50 border border-border/50">
              {experience.period}
            </span>
          </div>

          <ul className="space-y-2 mb-4">
            {experience.description.map((item, i) => (
              <motion.li
                key={i}
                initial={{ opacity: 0, x: -10 }}
                animate={isInView ? { opacity: 1, x: 0 } : { opacity: 0, x: -10 }}
                transition={{ duration: 0.3, delay: index * 0.15 + 0.3 + i * 0.1 }}
                className="text-muted-foreground text-sm leading-relaxed flex gap-2"
              >
                <span className="text-primary mt-1.5 flex-shrink-0">▹</span>
                {item}
              </motion.li>
            ))}
          </ul>

          <div className="flex flex-wrap gap-2">
            {experience.tech.map((tech) => (
              <span
                key={tech}
                className="px-3 py-1 text-xs font-mono bg-primary/10 text-primary rounded-full border border-primary/20"
              >
                {tech}
              </span>
            ))}
          </div>
        </div>
      </div>
    </motion.div>
  )
}

export function ExperienceSection() {
  const ref = useRef(null)
  const isInView = useInView(ref, { once: true, margin: "-100px" })

  return (
    <section id="experience" className="py-24 px-6">
      <div className="max-w-4xl mx-auto">
        <motion.div
          ref={ref}
          initial={{ opacity: 0, y: 20 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
          transition={{ duration: 0.5 }}
          className="flex items-center gap-4 mb-12"
        >
          <Briefcase className="w-6 h-6 text-primary" />
          <h2 className="text-3xl font-bold text-foreground">Experience</h2>
          <div className="flex-1 h-px bg-border" />
        </motion.div>

        <div className="relative">
          {experiences.map((exp, index) => (
            <ExperienceCard key={index} experience={exp} index={index} />
          ))}
        </div>
      </div>
    </section>
  )
}
